import { WiDaySleet } from "react-icons/wi";
import { FiExternalLink } from "react-icons/fi";
import { BsFillSunriseFill, BsFillSunsetFill } from "react-icons/bs";


const CurrentWeatherCard = ({ weatherData }) => {
    const monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']

    const currentDate = new Date();

    const getTime = (time) => {
        const time_epoch = new Date(time)
        let time_str
        if (time_epoch.getHours() < 10) {
            time_str = "0" + time_epoch.getHours() + ":" + time_epoch.getMinutes()
        } else {
            time_str = time_epoch.getHours() + ":" + time_epoch.getMinutes()
        }
        return time_str
    }

    // Weather parameters 
    const currentTemp = Math.round(Math.round(weatherData.weather_info.current.temp * 10) / 10)
    const sunrise = getTime(weatherData.weather_info.current.sunrise * 1000)
    const sunset = getTime(weatherData.weather_info.current.sunset * 1000)
    const realFeel = Math.round(Math.round(weatherData.weather_info.current.feels_like * 10) / 10)
    const humidity = weatherData.weather_info.current.humidity
    const cloudy = weatherData.weather_info.current.clouds
    const windSpeed = weatherData.weather_info.current.wind_speed
    const UVIndex = weatherData.weather_info.current.uvi
    const pressure = weatherData.weather_info.current.pressure
    // location info
    const cityName = weatherData.location_info.name
    const countryName = weatherData.location_info.country
    // last updated time
    const lastUpdate = getTime(currentDate.getTime())

    const dayName = currentDate.toString().split(' ')[0];
    return (

        <div className="sm:w-2/5 bg-main-bg-right text-white">
            <div className="flex flex-col mx-6">
                {/* Today section */}
                <div className="h-20 mt-10 md:mt-16">
                    <div className="flex flex-row justify-center">
                        <div className="w-10 grid">
                            <WiDaySleet className="place-self-center" style={{ color: 'white' }} size="62" />
                        </div>
                        <div className='flex flex-col'>
                            <div className="w-32">
                                <p className="text-white text-4xl text-center">Now</p>
                            </div>
                            <div className="w-32 mt-3">
                                <p className="text-center text-text-grayed text-sm">
                                    {dayName}, {currentDate.getDate()} {monthNames[currentDate.getMonth()]}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Current Temparature section */}
                <div className=" h-fit mt-3 md:mt-6">
                    <p className="text-center text-7xl">
                        {currentTemp}<span className="align-top text-4xl">&deg;c</span>
                    </p>
                    <div className="flex flex-row justify-center">
                        <div>
                            <p className="text-center text-xs font-light mt-3">
                                {cityName}, {countryName}

                            </p>
                        </div>
                        <div>
                            <FiExternalLink className='mt-3 ml-2' />
                        </div>
                    </div>
                </div>
                {/* Detail about the current */}
                <div className=" h-fit mt-8 md:mt-12 flex flex-row justify-around">
                    <div className="flex flex-col gap-10">
                        <div className="flex flex-col">
                            <div className="flex flex-row content-center gap-3">
                                <div>
                                    <BsFillSunriseFill size="25" />
                                </div>
                                <div>
                                    <h3 className="text-text-grayed">Sunrise</h3>
                                </div>
                            </div>
                            <div>
                                <p className="text-xl text-center">
                                    {sunrise}
                                </p>
                            </div>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">Rael feel</p>
                            <p className="text-xl text-center">
                                {realFeel}&deg;C
                            </p>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">Cloudy</p>
                            <p className="text-xl text-center">{cloudy}%</p>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">Wind speed</p>
                            <p className="text-xl text-center">{windSpeed}m/s</p>
                        </div>
                    </div>
                    <div className="flex flex-col gap-10">
                        <div className="flex flex-col">
                            <div className="flex flex-row content-center gap-3">
                                <div>
                                    <BsFillSunsetFill size="25" />
                                </div>
                                <div>
                                    <h3 className="text-text-grayed">Sunset</h3>
                                </div>
                            </div>
                            <div>
                                <p className="text-xl text-center">{sunset}</p>
                            </div>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">Humidity</p>
                            <p className="text-xl text-center">{humidity}%</p>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">Pressure</p>
                            <p className="text-xl text-center">{pressure}mbr</p>
                        </div>
                        <div>
                            <p className="text-xs text-center text-text-grayed">UV index</p>
                            <p className="text-xl text-center">{UVIndex}</p>
                        </div>
                    </div>
                </div>
                <div className=" mt-14 md:mt-7 lg:mt-5 xl:mt-5 2xl:mt-28">
                    <p className="text-xs text-center text-text-grayed">Last updated
                        on: {lastUpdate}
                    </p>
                </div>
            </div>
        </div>
    )
}

export default CurrentWeatherCard