import React, { useEffect, useState } from 'react'
import CurrentWeatherCard from "./components/CurrentWeatherCard";
import ForecastWeatherCard from "./components/ForecastWeatherCard";
import AppNavBar from './components/AppNavBar';
import CircularProgress from '@mui/material/CircularProgress';

const App = () => {

    const [data, setData] = useState(null)
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(null)
    let WeatherDate
    const searchText = (txt) => {
        const searchURL = "http://localhost:8080/weather_api/weather_data_city/" + txt
        fetch(searchURL).then(response => {
            return response.json()
        }).then(serverData => {
            // setData(serverData)
            if (!serverData.hasOwnProperty("error")) {
                setData(serverData)
            } else {
                window.alert(serverData.error.message)
            }
        }).catch((err) => {
            setError(err)
            console.log(err.message + " Hi i am form error")
        }).finally(() => {
            setLoading(false)
        })
    }

    useEffect(() => {
        const URL = "http://localhost:8080/weather_api/weather-data_ip"
        fetch(URL).then(response => {
            return response.json()
        }).then(serverData => {
            if (!serverData.hasOwnProperty("error")) {
                setData(serverData)
            } else {
                window.alert(serverData.error.message)
            }
        }).catch((err) => {
            setError(err)
            console.log(err.message + " Hi i am form error")
        }).finally(() => {
            setLoading(false)
        })
    }, [])

    // console.log("Is loading: " + loading)
    // console.log(data)
    console.log(error)
    // console.log(data)
    WeatherDate = data
    return (
        <div className="flex flex-col flex-wrap w-full">
            {/* top portion */}
            <div className="sticky top-0 w-full">
                <AppNavBar enteredText={searchText} />
            </div>
            {/* body portion */}


            {loading || data === null ? <div className="flex justify-center items-center h-screen"><CircularProgress /></div> :
                <div className="w-full flex flex-wrap flex-col-reverse sm:flex-row-reverse">
                    <ForecastWeatherCard weatherDataForecast={WeatherDate} />

                    <CurrentWeatherCard weatherData={WeatherDate} /></div>}
        </div>
    )

}

export default App